    let that = this;
    this.dataObjj={proId:this.$route.query.id,newBusId:'',type:'',evalBusinessId:'',adjBusinessId:''};
    let data = {
      proId: this.dataObjj.proId,
      newProId: this.dataObjj.newProId,
      type: this.dataObjj.type,
      evalBusinessId: this.dataObjj.evalBusinessId,
      adjBusinessId: this.dataObjj.adjBusinessId
    };
    data = JSON.stringify(data);
    this.axios
      .post("/risk/pro/selectProInfoAndEval", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        this.proInfo = response.data.proInfo;
        if (response.data.proRiskEval != null)
          this.proRiskEval = response.data.proRiskEval;
      })
      .catch(error => {});
    this.axios
      .post("/risk/pro/eval/selectDetailAdjInfo", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          this.adjustmentData = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .post("/risk/pro/proRiskEvalAndAdjHis", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.historyDetail = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .post("/risk/pro/eval/selectProEvalDetail", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.activeHeader = response.data.shift().detailVo;
          that.activeArr = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .post("/risk/pro/eval/getDetailByLevelNum", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
